<template>
    <div class="container">
        <ul class="timeline">
            <li>
                <h3>2009</h3>
                <p>High School</p>
            </li>
            <li>
                <h3>2012</h3>
                <p>Left High School</p>
            </li>
            <li>
                <h3>sep-2012</h3>
                <h3>sep-2013</h3>
                <p>Continente-Modelo</p>
                <img class="timeline-icon" src="../assets/img/continente-round.png" alt="">
            </li>
            <li>
                <h3>may-2014</h3>
                <h3>aug-2015</h3>
                <p>Intermarché</p>
                <img class="timeline-icon" src="../assets/img/intermarche-round.png" alt="">
            </li>
            <li>
                <h3>aug-2015</h3>
                <h3>jul-2016</h3>
                <p>Pingo Doce</p>
                <img class="timeline-icon" src="../assets/img/pingo-doce-round.png" alt="">
            </li>
            <li>
                <h3>jan-2018</h3>
                <h3>apr-2018</h3>
                <p>Academia de Código</p>
                <img class="timeline-icon" src="../assets/img/academia-de-codigo-round.png" alt="">
            </li>
            <li>
                <h3>may-2018</h3>
                <h3>present</h3>
                <p>Celfocus</p>
                <img class="timeline-icon" src="../assets/img/celfocus-round.png" alt="">
            </li>
        </ul>
    </div>

</template>

<script>
    export default {
        name: "Timeline",
        data: function () {
            return {}
        },
        created: function () {
            //document.addEventListener('scroll', timeline);

        },
        mounted: function () {
            document.addEventListener('scroll', this.timeline);
            this.timeline();

        },
        methods: {
            timeline: function () {
                var positionToTop = window.scrollY + window.innerHeight * 2 / 3;
                //compare scrolltop with scrolltop on each timeline event
                var timelineEntry = document.querySelectorAll('.timeline li');
                for (var i in timelineEntry) {
                    if (timelineEntry[i].offsetTop < positionToTop) {
                        timelineEntry[i].classList.add('visible');
                    } else {
                        this.removeVisibleClass(timelineEntry[i]);
                    }
                }
            },
            removeVisibleClass: function(element) {
                return element.className == 'visible' ? element.classList.remove('visible') : '';

            }


        }
    }
</script>
